body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  font-family: Arial, sans-serif;
  background-color: #fff;
}

.search-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 15vh;
}

.logo-section {
  text-align: center;
  margin-bottom: 30px;
}

.theo-logo {
  font-size: 90px;
  font-weight: 400;
  margin: 0;
  background: linear-gradient(45deg, #4285f4, #34a853, #fbbc05, #ea4335);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  letter-spacing: -2px;
}

.subtitle {
  color: #5f6368;
  font-size: 16px;
  margin-top: 5px;
}

.search-section {
  width: 100%;
  max-width: 584px;
  margin: 0 auto;
}

.search-box {
  display: flex;
  align-items: center;
  border: 1px solid #dfe1e5;
  border-radius: 24px;
  padding: 8px 16px;
  margin: 0 auto;
  max-width: 584px;
  transition: all 0.3s;
}

.search-box:hover,
.search-box:focus-within {
  box-shadow: 0 1px 6px rgba(32,33,36,.28);
  border-color: transparent;
}

.search-icon,
.mic-icon {
  color: #9aa0a6;
  font-size: 20px;
  padding: 0 8px;
}

.search-input {
  flex: 1;
  border: none;
  outline: none;
  font-size: 16px;
  padding: 8px;
  width: 100%;
}

.button-container {
  display: flex;
  justify-content: center;
  gap: 12px;
  margin-top: 24px;
}

.search-button {
  background-color: #f8f9fa;
  border: 1px solid #f8f9fa;
  border-radius: 4px;
  color: #3c4043;
  font-size: 14px;
  padding: 0 16px;
  height: 36px;
  cursor: pointer;
  transition: all 0.2s;
}

.search-button:hover {
  box-shadow: 0 1px 1px rgba(0,0,0,.1);
  background-color: #f8f9fa;
  border: 1px solid #dadce0;
  color: #202124;
}

.quick-links {
  display: flex;
  gap: 24px;
  margin-top: 40px;
}

.link-item {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.link-icon {
  font-size: 24px;
  background: #f8f9fa;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
}

.link-item:hover .link-icon {
  background: #e8eaed;
}

.link-item a {
  color: #202124;
  text-decoration: none;
  font-size: 14px;
}

.link-item:hover a {
  text-decoration: underline;
}

@media (max-width: 768px) {
  .search-container {
    padding-top: 10vh;
    padding: 10vh 20px 0;
  }

  .theo-logo {
    font-size: 64px;
  }
  
  .quick-links {
    flex-wrap: wrap;
    justify-content: center;
    gap: 16px;
  }
}

.answer-card {
  background: white;
  border: 1px solid #dfe1e5;
  border-radius: 8px;
  padding: 20px;
  margin-bottom: 16px;
  transition: all 0.2s ease;
}

.answer-card:hover {
  box-shadow: 0 1px 6px rgba(32,33,36,.28);
}

.answer-section {
  width: 100%;
  max-width: 584px;
  margin: 30px auto 0;
  padding: 20px;
}

.answer-header {
  margin-bottom: 12px;
}

.result-count {
  color: #70757a;
  font-size: 14px;
}

.answer-text {
  color: #202124;
  font-size: 16px;
  line-height: 1.6;
  margin: 0;
  white-space: pre-wrap;
}

.loading-icon {
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.error-message {
  color: #d93025;
  font-size: 14px;
  margin-top: 8px;
  text-align: center;
} 